.style {
    display: flex;
    width: 50vw;
    height: 70vh;
    margin: 0 auto;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.flex {
    display: flex;
    justify-content:space-between;
    align-items: center;
    flex-direction: row;
    margin: 0 auto;
}

.footer {
    display: flex;
    height: 30vh;
    justify-content:center;
    align-items: end;
    /* flex-direction: column; */
}
.footerUnit {
    display: flex;
    width: 100vw;
    justify-content:space-around;
    align-items: space-around;
    flex-direction: row;
    margin: 5px;
    background-color: black;
    padding: 5px;
}