.help {
    width: 20vw;
    height: 50vh;
    transform: translateX(140%);
    transition: transform 2s;
    position: relative;
    right: 70%;
    background-color: gainsboro;
    top: 50%;
    color: black;
    -webkit-box-shadow: -16px 0px 38px 11px rgba(0,0,0,0.79);
    box-shadow: -16px 0px 38px 11px rgba(0,0,0,0.79);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.bigger {
    font-size: 50px;
}