.scoreboardClass {
    transition-delay: transform 12s;
    transition: transform 1.5s;
    transform: translateY(150%);
    width: 40vw;
    height: 40vh;
    background-color: black;
    display: flex;
    justify-content: center;
    align-items: top;
    margin: 0 auto;
    opacity: 0.85;
    border-radius: 15px;
    color: white;
    padding: 5%;
    flex-direction: column;
    position:relative;
    top: -100%;
    -webkit-box-shadow: 0px 0px 38px 11px rgba(0,0,0,0.79);
    box-shadow: 0px 0px 38px 11px rgba(0,0,0,0.79);
  }
.link {
  background-color: black;
  color: white;
  width: 10%;
  padding: 1%;
  border: 1px solid rgb(9, 177, 9);
  background-color: rgb(22, 149, 39);
  text-decoration-line: none;
  position: absolute;
  right: 5vw;
  top: 35vh;
  border-radius: 15px;
}
.big {
  font-size: 60px;
  font-weight: bold;
  background-color: black;
}